import { Form, Input, message } from "antd";

export const CopyQuestionnaire = ({ questionnaire, copyQuestionnaire }) => {
  //  modal
  //  idnullnull10
  //    
  // 

  const handleSubmit = ({ title, description }) => {
    let newTitle, newDescription;
    newTitle = title;
    newDescription = description;
    if (title === undefined) {
      newTitle = questionnaire.title;
    }

    if (description === undefined) {
      newDescription = questionnaire.description;
    }

    const copy = {
      title: newTitle,
      description: newDescription,
      questions: questionnaire.questions,
      status: 1,
      answerCount: 0,
    };
    copyQuestionnaire(copy)
      .then((res) => {
        message.success("");
      })
      .catch((e) => {
        message.error("");
      });
  };

  return (
    <Form
      id={"copyForm"}
      onFinish={handleSubmit}
      preserve={false}
      style={{ marginTop: "3rem" }}
    >
      <Form.Item
        name={"title"}
        label={""}
        rules={[{ pattern: /^[^\s]*$/, message: "" }]}
      >
        <Input placeholder={""} type="text" id="title" />
      </Form.Item>
      <Form.Item
        name={"description"}
        label={""}
        rules={[{ pattern: /^[^\s].*$/, message: "" }]}
      >
        <Input
          placeholder={""}
          type="text"
          id="description"
        />
      </Form.Item>
    </Form>
  );
};
