import {
  Button,
  Descriptions,
  PageHeader,
  Statistic,
  Tag,
  Modal,
  Tooltip,
  message,
  Switch,
  Space,
} from "antd";
import { More } from "./more";
import dayjs from "dayjs";
import {
  useAddQuestionnaire,
  useDeleteQuestionnaire,
  useEditQuestionnaire,
} from "../../utils/questionnaire";
import { useQuestionnairesQueryKey } from "./util";
import { Link, useNavigate } from "react-router-dom";
import styled from "@emotion/styled";
import { EmptyQuestionnaires } from "./empty";
import { ButtonNoPadding } from "../../components/lib";
import {
  FolderViewOutlined,
  PieChartOutlined,
  PlayCircleOutlined,
  PauseCircleOutlined,
  RiseOutlined,
  CloudDownloadOutlined,
  ClockCircleOutlined,
  CopyOutlined,
  FileDoneOutlined,
} from "@ant-design/icons";
import { PageHeaderSkeletons } from "./pageheader-skeleton";
import copy from "copy-to-clipboard";
import { useEffect } from "react";
import { download } from "../../utils/excel";
import { EndTimePicker } from "./end-time-picker";
import { CopyQuestionnaire } from "./copy-questionnaire";

/** @jsxImportSource @emotion/react */
export const List = ({
  list,
  loading,
  deletes,
  setDeletes,
  hoverQuestionnaire,
  setHoverQuestionnaire,
  showAll,
  setShowAll,
}) => {
  // 
  // Skeleton
  // 
  //  message/
  //  copy
  // 
  //    disable refetch 
  //     <ClockCircleOutlined />
  //    
  // 
  const { mutateAsync: copyQuestionnaire, isLoading: copyLoading } =
    useAddQuestionnaire(useQuestionnairesQueryKey());

  const copyQuestionnaireHandler = (questionnaire) => {
    Modal.confirm({
      icon: null,
      title: `${questionnaire.title}`,
      okText: "",
      cancelText: "",
      content: (
        <CopyQuestionnaire
          questionnaire={questionnaire}
          copyQuestionnaire={copyQuestionnaire}
        />
      ),
      okButtonProps: {
        htmlType: "submit",
        form: "copyForm",
        loading: copyLoading,
      },
    });
  };

  useEffect(() => {
    if (deletes.length !== 0) {
      setShowAll(true);
    } else {
      setShowAll(false);
    }
  }, [deletes]);

  const mouseEnterHandler = (id) => {
    setHoverQuestionnaire(id);
  };

  const mouseLeaveHandler = (id) => {
    setHoverQuestionnaire(null);
  };

  // handle switch
  const switchHandler = (checked, evt, id) => {
    if (checked) {
      setDeletes([...deletes, id]);
    } else {
      const filtered = deletes.filter((quesId) => quesId !== id);
      setDeletes(filtered);
    }
  };

  //  copy to clipboard
  const copyHandler = (e, openCode) => {
    // FIXME 
    const copyLink = `http://localhost:3000/fill/${openCode}`;
    if (copy(copyLink)) {
      message.success(" ");
    } else {
      message.error("");
    }
  };

  // 
  const { mutateAsync: deleteQuestionnaire } = useDeleteQuestionnaire(
    useQuestionnairesQueryKey()
  );

  // 
  const { mutateAsync: editRelease, isLoading: releaseLoading } =
    useEditQuestionnaire(useQuestionnairesQueryKey());

  const navigate = useNavigate();

  return (
    <ListContainer>
      {loading ? (
        <PageHeaderSkeletons />
      ) : list.length === 0 ? (
        <EmptyQuestionnaires />
      ) : (
        list.map((questionnaire, index) => {
          const {
            id,
            title,
            description,
            status,
            answerCount,
            lastEditedTime,
            releaseTime,
            endTime,
            openCode,
          } = questionnaire;

          let tagColor, statusText, buttonText, releaseIcon, releaseColor;
          if (status === 1) {
            // 
            tagColor = "blue";
            statusText = "";
            buttonText = "";
            releaseIcon = <PlayCircleOutlined />;
            releaseColor = "#1E90FF";
          } else if (status === 2) {
            // 
            tagColor = "green";
            statusText = "";
            buttonText = "";
            releaseIcon = <PauseCircleOutlined />;
            releaseColor = "#2E8B57";
          } else {
            // 
            tagColor = "yellow";
            statusText = "";
            buttonText = "";
            releaseIcon = <ClockCircleOutlined />;
            releaseColor = "#FFFACD";
          }

          return (
            <Container
              key={index}
              onMouseEnter={() => mouseEnterHandler(id)}
              onMouseLeave={() => mouseLeaveHandler(id)}
            >
              <PageHeader
                css={{
                  "&:hover": {
                    // backgroundColor: "#F5F5DC",
                    backgroundColor: "#EEE9E9",
                  },
                }}
                ghost={false}
                title={title}
                tags={<Tag color={tagColor}>{statusText}</Tag>}
                subTitle={description || ""}
                extra={[
                  showAll || hoverQuestionnaire === id ? (
                    <Switch
                      key={"4"}
                      size={"small"}
                      checked={deletes.includes(id)}
                      onChange={(checked, evt) =>
                        switchHandler(checked, evt, id)
                      }
                    />
                  ) : null,
                  <More
                    key={"3"}
                    name={""}
                    operations={[
                      <ButtonNoPadding
                        icon={<FolderViewOutlined />}
                        type={"link"}
                      >
                        <span> </span>
                        <Link to={`${String(id)}/preview`}></Link>
                      </ButtonNoPadding>,
                      <ButtonNoPadding
                        icon={<PieChartOutlined />}
                        type={"link"}
                      >
                        <span> </span>
                        <Link to={`${String(id)}/analysis`}></Link>
                      </ButtonNoPadding>,
                      <ButtonNoPadding
                        icon={<FileDoneOutlined />}
                        type={"link"}
                      >
                        <span> </span>
                        <Link to={`${String(id)}/answers`}></Link>
                      </ButtonNoPadding>,
                      <ButtonNoPadding
                        icon={<CloudDownloadOutlined />}
                        type={"link"}
                        onClick={() => download(id)}
                      >
                        <span> </span>
                        
                      </ButtonNoPadding>,
                    ]}
                  />,
                  <More
                    key={"2"}
                    name={""}
                    operations={[
                      {
                        name: "",
                        handler: () => {
                          status === 2
                            ? Modal.info({
                                title: `${questionnaire.title}`,
                                content: "",
                                okText: "",
                              })
                            : status === 3
                            ? Modal.info({
                                title: `${questionnaire.title}`,
                                content: "",
                                okText: "",
                              })
                            : navigate(`${String(id)}/editing`);
                        },
                      },
                      <ButtonNoPadding
                        icon={<CopyOutlined />}
                        type={"link"}
                        onClick={() => copyQuestionnaireHandler(questionnaire)}
                      >
                        <span> </span>
                        
                      </ButtonNoPadding>,
                      {
                        name: "",
                        handler: () => {
                          Modal.confirm({
                            title: `${title}`,
                            content: "",
                            okText: "",
                            cancelText: "",
                            onOk() {
                              deleteQuestionnaire({ id })
                                .then(() => {
                                  message.success(`${title}`);
                                  if (deletes.includes(id)) {
                                    const filtered = deletes.filter(
                                      (quesId) => quesId !== id
                                    );
                                    setDeletes(filtered);
                                  }
                                })
                                .catch((e) => {
                                  message.error(`${title}`);
                                });
                            },
                          });
                        },
                      },
                    ]}
                  />,
                  <Button
                    key="1"
                    type="primary"
                    shape={"round"}
                    disabled={status === 3}
                    icon={releaseIcon}
                    loading={releaseLoading}
                    style={{
                      backgroundColor: releaseColor,
                      borderColor: releaseColor,
                    }}
                    onClick={() => {
                      if (status === 3) {
                        message.error("");
                        return;
                      }
                      editRelease({
                        id: questionnaire.id,
                        status: status === 1 ? 2 : 1,
                      })
                        .then(() => {
                          message.success(
                            `${
                              status === 1
                                ? `${title}`
                                : `${title}`
                            }`
                          );
                        })
                        .catch((e) => {
                          message.error(
                            `${
                              status === 1
                                ? `${title}`
                                : `${title}`
                            }`
                          );
                        });
                    }}
                  >
                    {buttonText}
                  </Button>,
                ]}
              >
                <div style={{ display: "flex" }}>
                  <div>
                    <div
                      style={{
                        display: "flex",
                        width: "max-content",
                      }}
                    >
                      <Statistic
                        title=""
                        value={answerCount}
                        // valueStyle={{ color: "#3f8600" }}
                        prefix={<RiseOutlined />}
                        style={{
                          marginRight: 32,
                        }}
                      />
                    </div>
                  </div>
                  <div
                    style={{
                      justifyContent: "flex-end",
                    }}
                  >
                    <Descriptions size="small" column={3}>
                      <Descriptions.Item label="">
                        {dayjs(lastEditedTime).format("YYYY-MM-DD hh:mm:ss")}
                      </Descriptions.Item>
                      <Descriptions.Item label="">
                        {releaseTime
                          ? dayjs(releaseTime).format("YYYY-MM-DD hh:mm:ss")
                          : ""}
                      </Descriptions.Item>
                      <Descriptions.Item label="">
                        <EndTimePicker
                          questionnaireStatus={status}
                          endTime={endTime}
                          questionnaireId={id}
                        />
                      </Descriptions.Item>
                      <Descriptions.Item label="">
                        {openCode ? (
                          <Space>
                            <Tooltip placement="topLeft" title={""}>
                              {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                              <a
                                onClick={(event) =>
                                  copyHandler(event, openCode)
                                }
                              >
                                
                              </a>
                            </Tooltip>
                            <a
                              href={`http://localhost:3000/fill/${openCode}`}
                              target={"_blank"}
                            >
                              
                            </a>
                          </Space>
                        ) : (
                          ""
                        )}
                      </Descriptions.Item>
                    </Descriptions>
                  </div>
                </div>
              </PageHeader>
            </Container>
          );
        })
      )}
    </ListContainer>
  );
};

const ListContainer = styled.div`
  width: 100%;
  background-color: #f5f5f5;
  flex: 1;
  overflow-x: auto;
  //::-webkit-scrollbar {
  //  display: none;
  //}
`;

const Container = styled.div`
  margin: 2rem 1.5rem 2rem 1.5rem;
`;
