import { Form, Input } from "antd";
import { LongButton } from "../../components/lib";
import { useAuth } from "../../context/auth-context";
import { useAsync } from "../../utils/use-async";

export const RegisterScreen = ({ onError }) => {
  const { register } = useAuth();
  const { run, isLoading } = useAsync(undefined, { throwOnError: true });

  const handleSubmit = async ({ username, password, cpassword }) => {
    if (password !== cpassword) {
      onError(new Error(""));
      return;
    }

    try {
      await run(register({ username, password }));
    } catch (e) {
      onError(e);
    }
  };

  return (
    <Form onFinish={handleSubmit}>
      <Form.Item
        name={"username"}
        rules={[{ required: true, message: "" }]}
      >
        <Input
          onChange={() => onError(null)}
          placeholder={""}
          type="text"
          id="username"
        />
      </Form.Item>
      <Form.Item
        name={"password"}
        rules={[{ required: true, message: "" }]}
      >
        <Input placeholder={""} type="password" id="password" />
      </Form.Item>
      <Form.Item
        name={"cpassword"}
        rules={[{ required: true, message: "" }]}
      >
        <Input placeholder={""} type="password" id="cpassword" />
      </Form.Item>
      <Form.Item>
        <LongButton loading={isLoading} type="primary" htmlType={"submit"}>
          
        </LongButton>
      </Form.Item>
    </Form>
  );
};
